html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
	margin: 0;
	padding: 0;
	border: 0;
	font-size: 100%;
	font: inherit;
	vertical-align: baseline;
}

body {
	background-color: rgb(0, 0, 0);
}

html {
	scrollbar-width: thin;
	scrollbar-color: rgb(71, 20, 71) #00a5a5;
}

::-webkit-scrollbar-thumb {
	background-color: rgb(71, 20, 71);
	border: none;
}

::-webkit-scrollbar {
	width: 4px;
	background-color: #00a5a5;
}

.section-title {
	background-color: rgb(4, 4, 4);
	font-size: 3rem;
	padding: 1rem 0;
	color: #ffcef8;
	font-family: "Julius Sans One", sans-serif;
	text-align: center;
	margin: 0 0 1rem 0;
}
@media (max-width: 900px) {
	.anchor {
		padding-top: 71px;
	}

	.section-title {
		font-size: 2rem;
		padding: 0.5rem;
	}
}
